// eslint-disable-file

import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"

import { rhythm } from "../utils/typography"
import styles from './bio.module.scss'

const Bio = ({showWrittenBy = true}) => {
  return (
    <StaticQuery
      query={bioQuery}
      render={data => {
        const { author } = data.site.siteMetadata
        return (
          <div 
            className={styles.bio_container} 
            style={{marginBottom: rhythm(1.5)}}
          >
            <div className={styles.horizontal_center}>
              <Image
                fixed={data.avatar.childImageSharp.fixed}
                alt={author}
                className={styles.bio_image_style}
                imgStyle={{ borderRadius: `50%` }}
              />
            </div>
            <div className={styles.name}>
              {showWrittenBy ? 'Written by' : null} {author}
            </div>
            <div className={styles.bio_description}>
              Programmer, writer, cat-lover and other bullshits someone has in bio.
            </div>
          </div>
        )
      }}
    />
  )
};

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 75, height: 75) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        social {
          twitter
        }
      }
    }
  }
`

export default Bio
